.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

h1{
  font-size:18px;
  margin-bottom:20px;
}

#courses{
  list-style: none;
  display: inline-block;
  width: 500px;
  text-align: left;
}

#courses p{
  display: block;
  padding: 20px 20px;
  background-color: #C5EFF7;
  color: #000;
  position: relative;
  cursor: pointer;

  -webkit-transition: 0.3s;

  -o-transition: 0.3s;

  transition: 0.3s;
}

#courses p b{
  position: absolute;
  right: 30px;
  width: 100px;
  color: #000;
  text-align: right;
}

#courses p:hover{
  background-color:#52B3D9;
  color:#fff;
}

#courses p.active{
  color:#fff;
  background-color:#446CB3;
}

#courses p.active b{
  color: #fff;
}


#total{
  background: #3FC380 !important;
  cursor: default !important;
  padding: 10px !important;
  float: right;
}



